
import { Component,NgZone } from '@angular/core';
import { Router } from '@angular/router';
import { Platform } from '@ionic/angular';
import { CallKitVoip } from 'capacitor-callkit-voip';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss'],
})
export class AppComponent {
  constructor( private platform: Platform,private route: Router,private zone: NgZone) {
    this.initializeApp();
  }
  initializeApp() {
    this.platform.ready().then(() => {
     // Trigger the push setup
      this.registerVoipNotification();
    });
  }

    async registerVoipNotification(){
    // register token
    CallKitVoip.addListener('registration', (({token}) =>
      console.log(`VOIP token has been received ${token}`)
    ));
  CallKitVoip.addListener('callAnswered', (({username, connectionId}) => {
      // handle call (e.g. redirect it to specific page with call)
      console.log(`Call has been started with ${username} (connectionId: ${connectionId})`);
      this.zone.run(async () => {
      this.route.navigate(['home']);
      });
  }));
    CallKitVoip.addListener('callStarted', (({username, connectionId}) => {
      // handle call (e.g. redirect it to specific page with call)
      console.log(`Call has been started with ${username} (connectionId: ${connectionId})`);
  }));
// init plugin, start registration of VOIP notifications
    await CallKitVoip.register(); // can be used with `.then()`
    console.log('Push notification has been registered');
  }

}
